angular.module('component-spinner', [])
	.directive('ctSpinner', ctSpinnerDirective);


function ctSpinnerDirective() {
	return {
		restrict: 'A',
		link: postLink
	};

	function postLink(scope, element, attrs) {
		var $spinner = angular.element(require('./spinner.html'));

		element.addClass('ct-loader');

		scope.$watch(attrs.ctSpinner, function(value) {
			if (value) {
				element.append($spinner);
			} else {
				$spinner.remove();
			}
		});
	}
}



